/* ---------- Product Thumbnail  Css Start ---------- */
.productThumbnail {
  display: inline-block;
  position: relative;
  overflow: hidden;
  margin: 3px; }

.productThumbnail__img {
  border: 1px solid lightgrey;
  width: 100%; }

.productThumbnail__link {
  position: absolute;
  right: 5px;
  bottom: 45px; }

.productThumbnail__link__search {
  z-index: 7;
  top: 87px;
  left: 120%;
  right: 0;
  color: #fff;
  font-size: 17px !important;
  background-color: #19a78d;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  padding-top: 6px;
  padding-left: 8px;
  opacity: 0.75; }



/* ---------- //Portfolio images Icon Section End ---------- */
@media(max-width: 900px) {
  .productThumbnail__link__search {
    left: 60%; } }
/* _________ //Portfolio End _________ */
