@import '../appBasicStyleSetting';

/**Product Show Panel CSS start**/
.productShow-panel {
  display: flex;
  align-items: center;
  justify-content: center; }

.productShow-panel__qty {
  flex: 2;
  font-size: large;
  text-align: center;
  color: $potmasterTitleColor; }

.productShow-panel__qtyInput {
  flex: 3;
  margin-right: 5px;
  text-align: center;
  font-size: large; }

.productShow-panel_addToCart,
.productShow-panel_addToFavorite {
  flex: 3; }

.productShow-panel_addToCart__btn,
.productShow-panel__addToFavorite__btn {
  border: none;
  width: 90%;
  cursor: pointer;
  padding: 3px 10px;
  background: #ccc;
  color: $potmasterTitleColor;
  text-align: center; }

.productShow-panel_addToCart__btn:hover,
.productShow-panel__addToFavorite__btn:hover {
  color: darkred;
  background: #bbb; }
/**Product Show Panel CSS end**/

